import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchImages } from '../../actions/searchAction';
import { fetchCategory } from '../../actions/searchAction';

import { withStyles } from '@material-ui/core/styles';
import compose from 'recompose/compose';
import withWidth from '@material-ui/core/withWidth';
import { filtersStylesPattern } from '../../CustomMaterialUIStyles/FilterComponents';

import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import InputLabel from '@material-ui/core/InputLabel';

import RadioButtonChecked from '@material-ui/icons/RadioButtonChecked';
import RadioButtonUnchecked from '@material-ui/icons/RadioButtonUnchecked';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';

const styles = theme => (filtersStylesPattern(theme))

class ImgCategory extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false
    }
  }

  static getDerivedStateFromProps = (props, state) => {
    if (props.showFiltersBar !== true) {
      state.open = false
    }
    return null
  }

  handleListHeaderClick = () => {
    this.setState({ open: !this.state.open });
  };

  handleListItemClick = (val) => {
    this.props.fetchCategory(val);
    this.props.fetchImages();
    this.setState({ open: !this.state.open });
  };

  render() {
    const { classes, showFiltersBar, category } = this.props;

    const options = ['fashion', 'nature', 'backgrounds', 'science', 'education', 'people',
    'feelings', 'religion', 'health', 'places', 'animals', 'industry', 'food', 'computer', 
    'sports', 'transportation', 'travel', 'buildings', 'business', 'music'];

    const SetIcon = (x) => {
      if (category === x) {
        return <RadioButtonChecked className={classes.MenuItemIcon} />
      } else {
        return <RadioButtonUnchecked className={classes.MenuItemIcon} />
      }
    }

    const CustomControl = (
      <Select
        name='category'
        className={classes.rootSelectWrap}
        classes={{
          root: classes.rootSelect,
          select: classes.select,
        }}
        autoWidth
        value={category}
        renderValue={() => <span>category: <span className={classes.selectedValue}>{(category=='') ? 'all' : category}</span> </span>}
        onChange={(e) => { this.props.fetchCategory(e.target.value); this.props.fetchImages() }}
        disableUnderline
        displayEmpty
        MenuProps={{
          className: classes.selectedMenuPaper,
          anchorOrigin: {
            vertical: 'top',
            horizontal: 'left',
          },
          // for anchorOrigin:
          getContentAnchorEl: null,
          MenuListProps: {
            className: 'SelectedMenuList',
            classes: {
              padding: classes.MenuPropsListPadding
            }
          },
          BackdropProps: {
            invisible: false,
            className: classes.backDropRoot
          },
        }}
      >
        <MenuItem
          classes={{
            root: classes.MenuItemDisabled,
          }}
          disabled
          divider
          dense
        >
          <span className={classes.MenuItemDisabledText}>category: </span><em className={classes.MenuItemDisabledHook}>{category}</em>
          </MenuItem>
        <MenuItem
          value=''
          dense
          divider
        >
          {SetIcon('')}
          <span className={classes.MenuItemText}>ALL</span>
        </MenuItem>
        {options.map(option => (
          <MenuItem
            key={option}
            value={option}
            dense
          >
            {SetIcon(option)}
            <span className={`${classes.MenuItemText} filtersMenuItemText`}> {option} </span>
          </MenuItem>
        ))}
      </Select>
    )

    const NativeControl = (
      <div className={classes.rootNativeControl}>
        <InputLabel
          className={classes.nativeControlInputLabel}
          focused={false} 
          htmlFor="age-native-simple"
          shrink
        >
          category
        </InputLabel>
        <Select
          native
          name='category'
          className={`${classes.rootSelectWrap} ${classes.selectedValue}`}
          classes={{
            root: classes.rootSelect,
            select: classes.select,
          }}
          autoWidth
          value={category}
          onChange={(e) => { this.props.fetchCategory(e.target.value); this.props.fetchImages() }}
          disableUnderline
        >
          <option disabled>
            category:
          </option>
          <option className={classes.nativeControlOption} value={''}>
            all
          </option>
          {options.map(option => (
            <option key={option} className={classes.nativeControlOption} value={option}>
              {option}
            </option>
          ))}
        </Select>
      </div>
    )

    // set form type depends on device type (mobile devices will have native form selector)
    let formSelectorType;
    if (/Mobi/i.test(navigator.userAgent) || /Android/i.test(navigator.userAgent)) {
      formSelectorType = NativeControl
    } else {
      formSelectorType = CustomControl
    }

    // Fix flicker in Safari 9 when collapse finishes entering
    const removeTransition = node => {
      node.style.transition = '0s';
      requestAnimationFrame(() => {
        node.style.transitiіon = '';
      });
    };

    return (
      <FormControl className={classes.formControl}>

        {/* form type for screens with the smallest width resolution ('xs'/portrait) */}
        <List className={classes.rootListWrap}>
          <ListItem
            button
            classes={{
              root: classes.listItemHeader,
              button: classes.listItemHeaderButton,
            }}
            onClick={this.handleListHeaderClick}
          >
            <ListItemText
              // className={classes.listItemHeaderText}
              classes={{ primary: classes.listItemTextHeader }}
              primary={
                <span>
                  category: {this.state.open ? null : <span className={classes.selectedValue}>{(category == '') ? 'all' : category}</span>}
                </span>
              }
            />
            {this.state.open ? <ExpandLess /> : <ExpandMore />}
          </ListItem>
          <Collapse in={(showFiltersBar && this.state.open)} timeout="auto" unmountOnExit onEntered={removeTransition}>
            <List component="div" className={classes.listCollapsedItems} disablePadding>
              <ListItem
                button
                classes={{
                  root: classes.listItemNested,
                  button: classes.listItemNestedButton,
                }}
                onClick={() => this.handleListItemClick('')}
                divider
              >
                {SetIcon('')}
                <ListItemText
                  inset
                  classes={{ primary: classes.listItemTextNested }}
                  primary={<span>ALL</span>}
                />
              </ListItem>
              {options.map(option => (
                <ListItem
                  key={option}
                  button
                  classes={{
                    root: classes.listItemNested,
                    button: classes.listItemNestedButton,
                  }}
                  onClick={() => this.handleListItemClick(option)}
                >
                  {SetIcon(option)}
                  <ListItemText
                    inset
                    classes={{ primary: classes.listItemTextNested }}
                    primary={<span>{option}</span>}
                  />
                </ListItem>
              ))}
            </List>

          </Collapse>
        </List>

        {/* form type for all another screens */}
        {formSelectorType}

      </FormControl>
    )
  }
}

ImgCategory.propTypes = {
  fetchImages: PropTypes.func.isRequired,
  fetchCategory: PropTypes.func.isRequired,
  category: PropTypes.string.isRequired,
  showFiltersBar: PropTypes.bool,
}

const mapStateToProps = state => ({
  category: state.search.category,
})

export default connect(mapStateToProps, { fetchImages, fetchCategory })(compose(withStyles(styles), withWidth(), )(ImgCategory))


