export const CustomStyles = {
  dropDownMenuProps: {
    anchorOrigin: {
      vertical: 'bottom',
      horizontal: 'left',
    },
    menuStyle: {
      // overflowX: 'hidden',
      // minWidth: '120',
      // width: '100%',
      // width: '100%',
    }
  },

  // // className='filtersBar'
  filtersBar: {
    display: 'flex',
    flexFlow: 'row',
    backgroundColor: '#fff',
    // width: '100%',
    paddingTop: '3px',
    margin: '0px 2px',
    borderBottom: '2px solid #fff',
  },

  iconSelectField: {
    padding: 0,
    margin: '5px 0px 0px 0px',
    height: 24,
    width: 24,
  },
  labelSelectField: {
    height: '1.7em',
    lineHeight: '2em',
    fontSize: '1em',
    color: '#777',
    top: 0,
    opacity: 1,
    padding: "0px 25px 0px 10px",
    textAlign: 'center',
  },
  menuDivider: {
    marginLeft: '0px',
    marginTop: '0px',
    marginBottom: '0px',
  },
  rootSelectField: {
    width: '100%',
    height: '2em',
    margin: '0px',
    borderRadius: 2,
    // borderBottom: null,
    fontSize: '1em',
  },  
  selectFieldList: {
    paddingTop: '0.3em',
    paddingBottom: '0px',
    borderTop: 'none',
  },
  selectFieldRootMenuItem: {
    height: '2em',
    minHeight: '2em',
    lineHeight: '2em',
    fontSize: '1em',
  },
  selectFieldMenuItem: {
    textAlign: 'left',
  },
  underlineSelectField: {
    display: 'none',
    color: '#000',
  },
};

export const CustomStylesDesignType_5 = {
  dropDownMenuStyle: { 
    menuStyle :{
      width: '100vw',
    },
  },
  labelSelectField: {
    color: '#555',
  },
  rootSelectField: {
    borderBottom: '1px solid #f9f9f9',
    backgroundColor: '#eee',
  },
  selectFieldMenuItem: {
    textTransform: 'uppercase',
  },
  iconSelectField: {
    fill: '#888',
  },
};

export const filtersStylesPattern = (theme) => {
  return {
    dialogPaperWidthSm: {
      maxWidth: '80vw',
      maxHeight: '80vh',
      width: 'inherit',
      height: 'inherit',

    },
    formControl: {
      width: '100%',
    },
    rootSelectWrap: {
      width: '100%',
      height: '2em',
      margin: '0px',
      borderRadius: 2,
      fontSize: '1em',
      color: '#777',
      [theme.breakpoints.only('xs')]: {
        display: 'none',
      },
    },
    selectedValue: {
      height: '1em',
      color: 'rgb(0, 0, 138)',
    },
    rootNativeControl: {
      display: 'flex',
      flexWrap: 'wrap',
      [theme.breakpoints.only('xs')]: {
        display: 'none',
      },
    },
    rootListWrap: {
      display: 'none',
      padding: 0,
      [theme.breakpoints.only('xs')]: {
        display: 'block',
      },
      marginBottom: '2px',
    },
    rootSelect: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'space-between',
    },
    select: {
      padding: '0 24px 0 12px',
      textAlign: 'center',
      lineHeight: '2em',
      // fontSize: '1.1em',
      [theme.breakpoints.only('xs')]: {
        padding: '0 24px',
      },
    },
    nativeControlInputLabel: {
      marginLeft: 12,
      fontSize: '1em',
    },
    nativeControlOption: {
      color: '#000',
    },
    MenuPropsListPadding: {
      padding: '0 0 2px 0',
      boxSizing: 'border-box',
    },
    // MenuPropsItem: {
    //   justifyContent: 'space-between',
    // },
    MenuItemText: {
      padding: '0 6px 0 10px',
      color: '#000',
      width: '100%',
    },
    MenuItemIcon: {
      width: '0.6em',
      height: '0.6em',
      fill: 'rgba(0,0,0,0.8)',
    },
    MenuItemIconMultipleSelection: {
      width: '0.6em',
      height: '0.6em',
      fill: 'rgba(0,0,0,0.8)',
      fontSize: '1.8em',
    },
    MenuItemDisabled: {
      opacity: '1',
      paddingTop: '0.8em',
      color: '#fff',
      backgroundColor: '#666',
    },
    MenuItemDisabledText: {
      textTransform: 'uppercase',
    },
    MenuItemDisabledHook: {
      visibility: 'hidden',
    },
    backDropRoot: {
      backgroundColor: 'rgba(0,0,0,0.2)',
    },
    listItemHeader: {
      height: '2em',
      backgroundColor: 'rgba(38,50,56,0.12)',
    },
    listItemHeaderButton: {
      '&:hover': {
        backgroundColor: 'rgba(38,50,56,0.12)',
      },
    },
    listItemTextHeader: {
      fontSize: '1.05em', 
      whiteSpace: 'nowrap',
      textOverflow: 'ellipsis',
      overflow: 'hidden',
    },
    listItemTextNested: {
      fontSize: '1.05em',
      textTransform: 'uppercase',
    },
    listItemNested: {
      height: '2em',
    },
    listItemNestedButton: {
      '&:hover': {
        backgroundColor: 'rgba(38,50,56,0.04)',
      },
    },
    listCollapsedItems: {
      overflowY: 'scroll',
      maxHeight: '52vh',
    },
    selectedMenuPaper: {
      maxWidth: 300,
    },

    // for 'native' color menu
    selectedMenuModal: {
      justifyContent: 'center',
      alignItems: 'center',
    },
    selectedMenuPaperRoot: {
      // top: '50%',
      // left: '50%',
      // transform: 'translate(-50%, -50%)',
      position: 'static',
      maxHeight: '80vh',
    },
  }
}


