import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';
import compose from 'recompose/compose';
import withWidth from '@material-ui/core/withWidth';

import ImgOrientation from './SearchFilters/ImgOrientation';
import ImgCategory from './SearchFilters/ImgCategory';
import ImgColor from './SearchFilters/ImgColor';
import ImgType from './SearchFilters/ImgType';
import ImgOrder from './SearchFilters/ImgOrder';

import Divider from '@material-ui/core/Divider';

const styles = theme => ({
  filtersBar:{
    display: 'flex',
    flexFlow: 'row',
    justifyContent: 'space-evenly',
    backgroundColor: '#fff',
    paddingTop: '3px',
    margin: '0px 2px',
    borderBottom: '2px solid #fff',
    fontSize: '1em',
    [theme.breakpoints.down('md')]: {
      justifyContent: 'space-around',
    },
    [theme.breakpoints.only('xs')]: {
      flexFlow: 'column',
    },
    '& > div': {
      maxWidth: '20%',
      [theme.breakpoints.only('xs')]: {
        maxWidth: '100%',
      },
      '&:hover': {
        // backgroundColor: '#eee',
        borderRadius: 2,
      },
    }
  },
})

class SearchFilters extends Component {
  render() {
    const { classes, showFiltersBar } = this.props;

    return (
      <div>
        {/* {showFiltersBar ? ( */}
        <div className={`${classes.filtersBar} filtersBar`}>
          <div> <ImgOrder showFiltersBar={showFiltersBar} /> </div>
          <div> <ImgOrientation showFiltersBar={showFiltersBar} /> </div>
          <div> <ImgCategory showFiltersBar={showFiltersBar} /> </div>
          <div> <ImgColor showFiltersBar={showFiltersBar} /> </div>
          <div> <ImgType showFiltersBar={showFiltersBar} /> </div>
        </div>
        {/* ) : setTimeout(() => { null }, 400)} */}
        <Divider />
      </div>
    )
  }
}

