import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { fetchImages } from '../actions/searchAction';
import { fetchSearchText } from '../actions/searchAction';
import { fetchWindowWidth } from '../actions/searchAction';

import MenuDrawer from './Drawer';
import SearchFilters from './Filters';
// import ImgSortIcon from './SearchFilters/ImgSortIcon';

import { withStyles } from '@material-ui/core/styles';
import compose from 'recompose/compose';
import withWidth from '@material-ui/core/withWidth';

import IconButton from '@material-ui/core/IconButton';
import Input from '@material-ui/core/Input';
import FormControl from '@material-ui/core/FormControl';
import AppBar from '@material-ui/core/AppBar';

import Search from '@material-ui/icons/Search';
import Menu from '@material-ui/icons/Menu';
import MoreVert from '@material-ui/icons/MoreVert';
import ExpandMore from '@material-ui/icons/ExpandMore';
import ExpandLess from '@material-ui/icons/ExpandLess';

// import { push } from 'react-router-redux';
// import { Route } from 'react-router-dom';

const styles = theme => ({
  appBar: {
    backgroundColor: 'rgb(38,50,56)',
    height: 57,
    padding: '0px 4px',
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  appBarInputGroup: {
    marginLeft: 4,
    width: '75%',
    display: 'flex',
    alignItems: 'center',
  },
  appBarLastChildGroup: {
    display: 'inline-flex',
  },
  appBarIcons: {
    color: '#fff',
  },
  filtersButton: {
    height: '1.6em',
    width: '1.6em',
    padding: '0',
    transition: 'all 0.3s ease',
  },
  filtersIcon: {
    color: '#263238',
  },
  rootComponent: {
    position: 'fixed',
    width: '100%',
    top: 0,
    zIndex: 1001,
  },
  searchFieldInput: {
    padding: '6px 0px 4px',
    width: '100%',
  },
  searchFieldInputRoot: {
    paddingLeft: 5,
    fontSize: '1.2em',
    lineHeight: '1.2em',// 2.em
    height: '2em',
  },
  searchFiltersTop: {
    position: 'relative',
    top: 57,
    [theme.breakpoints.only('xs')]: {
      display: 'none',
    },
  },
  searchFiltersBody: {
    width: '100%',
    transition: 'all 0.4s ease',
  },
  searchFiltersWrap: {
    position: 'fixed',
    top: 0,
    height: 57,
    width: '100%',
    display: 'flex',
    alignItems: 'end',
    [theme.breakpoints.only('xs')]: {
      top: -200,
    },
  },
  searchFieldWrap: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    width: '100%',
    height: '2em',
    backgroundColor: '#f5f5f5',
    borderRadius: 1,
  },
})

class NavBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showDrawer : false,
      showFilters: false,
      windowTop: true,
    };
    this.handleResize = this.handleResize.bind(this);
    this.handleScroll = this.handleScroll.bind(this);
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleResize);
    window.addEventListener('scroll', this.handleScroll);
    this.props.fetchImages();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
    window.removeEventListener('scroll', this.handleScroll);
  }
  
  handleResize() {
    let width = window.innerWidth;
    let currentWidth;
    
    switch (true) {
      case (width >= 1600):
        currentWidth = 1
        break;
      case (width >= 1024):
        currentWidth = 2
        break;
      case (width >= 768):
        currentWidth = 3
        break;
      case (width >= 480):
        currentWidth = 4
        break;
      default:
        currentWidth = 5
    }

    if (this.props.designType === currentWidth){
      return null
    } else {
      (currentWidth === 5) ? (this.setState({ showFilters: false })) : null;
      this.props.fetchWindowWidth(currentWidth)
    }
  }

  handleScroll() {
    if (window.pageYOffset < 10) {
      this.setState({
        windowTop: true,
        showFilters: false,
      })
    } else {
      this.setState({
        windowTop: false,
      })
    }
  }

  handleMenuButtonClick = () => {
    this.setState({
      showDrawer: !this.state.showDrawer,
    })
  }

  handleFiltersButtonClick = () => {
    this.setState({
      showFilters: !this.state.showFilters,
    })
  }

  // for filtersToolBar animation:
  filtersTransform = (breakpoint) => {
    console.log(breakpoint, 'breakpoint');
    if (breakpoint === 'all') {
      return { transform: 'translate(0px, 57px)' }
    } else {
      return { transform: 'translate(0px, 257px)' }
    }
  }

  render() {
    const { classes, width, location } = this.props;

    let searchHint = 'Search...';

    let filtersType, filtersIcon, navigationMenuIcon, searchIcon, sortIcon, searchFiltersTop;

    switch (true) {
      case (location.pathname === '/'):
        filtersType = <SearchFilters showFiltersBar={this.state.showFilters}/>
        break;
      case (location.pathname === '/favorites'):
        filtersType = null
        break;
      default:
        break;
    }

    const _filtersIcon = (
      <IconButton
        onClick={this.handleFiltersButtonClick}
        className={classes.filtersButton}
       >
        {this.state.showFilters ? <ExpandLess className={classes.filtersIcon} /> : <ExpandMore className={classes.filtersIcon} />}
      </IconButton>
    );

    const _navigationMenuIcon = (
      <IconButton
        onClick={this.handleMenuButtonClick}
      >
        <Menu className={classes.appBarIcons} />
      </IconButton>
    );

    const _navigationMoreVertIcon = (
      <IconButton
      // onClick={this.handleMenuButtonClick}
      >
        <MoreVert className={classes.appBarIcons} />
      </IconButton>
    );

    const _searchIcon = (
      <IconButton
        onClick={() => this.props.fetchImages()}
      >
        <Search className={classes.appBarIcons} />
      </IconButton>
    );

    const _searchFiltersTop = (
      <div className={classes.searchFiltersTop}>
        {/* <SearchFilters /> */}
        {filtersType}
      </div>
    );

    const _searchFiltersBody = (
      <div 
        className={classes.searchFiltersBody}
        // style={this.state.showFilters ? filtersTransform('all') : null}
        style = { 
          (() => {
            console.log(width, 'width', this.props.designType, 'this.props.designType');
          if (this.state.showFilters) {
            return (
              (this.props.designType===5) ? this.filtersTransform('xs') : this.filtersTransform('all') 
            )
          } else return null
          })()
        }
        >
        {/* <SearchFilters /> */}
        {filtersType}
      </div>
    );

    if (this.props.designType===5) {
      searchIcon = null;
      sortIcon = null;
      filtersIcon = _filtersIcon;
      navigationMenuIcon = true;
    } else {
      // sortIcon = <ImgSortIcon />;
      searchIcon = _searchIcon;
      // searchFiltersTop = _searchFiltersTop;
      navigationMenuIcon = false;
      if (this.state.windowTop) {
        filtersIcon = null;
      } else {
        filtersIcon = _filtersIcon;
      }
    }

    const menuDrawer = (
      <MenuDrawer
        value={this.state.showDrawer}
        side={navigationMenuIcon ? 'right' : 'left'}
        onChange={(val) => this.setState({ showDrawer: val })}
      />
    );

    console.log(this.props.location);

    return (
      <div>
        <div className={classes.rootComponent}>
          <AppBar
            className={classes.appBar}
          >
            {(!navigationMenuIcon) ? _navigationMenuIcon : null}
            <div className={classes.appBarInputGroup}>
              {searchIcon}
              <div className={classes.searchFieldWrap}>
                <FormControl
                >
                  <Input
                    classes={{
                      root: classes.searchFieldInputRoot,
                      input: classes.searchFieldInput,
                    }}
                    value={this.props.searchText}
                    onChange={(e) => { this.props.fetchSearchText(e.target.value); this.props.fetchImages() }}
                    placeholder={searchHint}
                    disableUnderline={true}
                    autoFocus
                  />
                </FormControl>
                {filtersIcon}
              </div>
              {sortIcon}
            </div>
            <div className={classes.appBarLastChildGroup}>
              {_navigationMoreVertIcon}
              {navigationMenuIcon ? _navigationMenuIcon : null}
            </div>
          </AppBar>
          {/* {this.state.showFilters ? _searchFiltersBody : null} */}
          <div className={classes.searchFiltersWrap}>
            {_searchFiltersBody}
          </div>
          {this.state.showDrawer ? menuDrawer : null}
        </div>
        {_searchFiltersTop}
      </div>  
    )
  }
}

NavBar.propTypes = {
  fetchSearchText: PropTypes.func.isRequired,
  fetchImages: PropTypes.func.isRequired,
  fetchWindowWidth: PropTypes.func.isRequired,
  searchText: PropTypes.string.isRequired,
  designType: PropTypes.number.isRequired,
  classes: PropTypes.object.isRequired,
  width: PropTypes.string.isRequired,
}

const mapStateToProps = state => ({
  searchState: state,
  searchText: state.search.searchText,
  designType: state.search.designType,
})

const mapDispatchToProps = {
  fetchImages,
  fetchSearchText,
  fetchWindowWidth,
}

export default connect(mapStateToProps, mapDispatchToProps)(compose(withStyles(styles), withWidth(),)(NavBar))


