import { 
  FETCH_IMAGES,
  FETCH_SEARCH_TEXT,
  FETCH_AMOUNT,
  FETCH_ORDER,
  FETCH_ORIENTATION,
  FETCH_IMAGE_TYPE,
  FETCH_CATEGORY,
  FETCH_COLOR,
  FETCH_PAGE,
  FETCH_MORE_IMAGES,
  FETCH_WINDOW_WIDTH,
  CUT_DOWN_SEARCH_STORE,
  ADD_TO_FAVORITES,
} from '../actions/types';

// determine 'designType' before all components rendering 
let _designType, _amount;
switch (true) {
  case (window.innerWidth >= 1600):
    _designType = 1;
    _amount = 80;
    break;
  case (window.innerWidth >= 1024):
    _designType = 2;
    _amount = 60;
    break;
  case (window.innerWidth >= 768):
    _designType = 3;
    _amount = 60;
    break;
  case (window.innerWidth >= 480):
    _designType = 4;
    _amount = 60;
    break;
  default:
    _designType = 5;
    _amount = 30;
    break;
}

const initialState = {
  searchText: '',
  order: 'popular',
  orientation: 'horizontal',
  imageType: 'photo',
  category: '',
  color: [null],
  images: [],
  page: 1,
  total: 0,

  amount: _amount,
  designType: _designType,
  favorites: [],
};

export default function(state = initialState, action) {
  switch (action.type) {
    case FETCH_SEARCH_TEXT:
      return Object.assign({}, state, {
        searchText: action.payload,
      })
    case FETCH_IMAGES:
      return Object.assign({}, state, {
        images: action.payload[0],
        total: action.payload[1],
        page: 1,
      })
    case FETCH_AMOUNT:
      return Object.assign({}, state, {
        amount: action.payload,
      })
    case FETCH_ORDER:
      return Object.assign({}, state, {
        order: action.payload,
      }) 
    case FETCH_ORIENTATION:
      return Object.assign({}, state, {
        orientation: action.payload,
      }) 
    case FETCH_IMAGE_TYPE:
      return Object.assign({}, state, {
        imageType: action.payload,
      })
    case FETCH_CATEGORY:
      return Object.assign({}, state, {
        category: action.payload,
      })
    case FETCH_COLOR:
      return Object.assign({}, state, {
        color: action.payload,
      })
    case FETCH_PAGE:
      return Object.assign({}, state, {
        page: action.payload,
      })
    case FETCH_MORE_IMAGES:
      return Object.assign({}, state, {
        images: state.images.concat(action.payload),
      })
    case FETCH_WINDOW_WIDTH:
      return Object.assign({}, state, {
        designType: action.payload,
      })
    case CUT_DOWN_SEARCH_STORE:
      return Object.assign({}, state, {
        images: state.images.slice(0, state.amount),
        page: 1,
      })
    case ADD_TO_FAVORITES:
      return Object.assign({}, state, {
        favorites: action.payload,
      })
    default:
      return state;
  }
}


