import { 
  FETCH_IMAGES,
  FETCH_SEARCH_TEXT,
  FETCH_AMOUNT,
  FETCH_ORDER,
  FETCH_ORIENTATION,
  FETCH_IMAGE_TYPE,
  FETCH_CATEGORY,
  FETCH_COLOR,
  FETCH_PAGE,
  FETCH_MORE_IMAGES,
  FETCH_WINDOW_WIDTH,
  CUT_DOWN_SEARCH_STORE,
  ADD_TO_FAVORITES,
} from './types';

import { fetchViewerImg, fetchViewerOpen } from './imgViewerAction';

export const fetchImages = () => (dispatch, getState) => {
  let {
    searchText,
    amount,
    order,
    orientation,
    imageType,
    category,
    color,
  } = getState().search;
  fetch(`https://pixabay.com/api/?key=8913420-89429fef6031f24fd40903778&q=${searchText}&image_type=${imageType}&per_page=${amount}&order=${order}&orientation=${orientation}&category=${category}&colors=${color.join()}&safesearch=true`)
    .then(response => response.json())
    .then(response => {
      window.scrollTo(0, 0);      
      dispatch({
        type: FETCH_IMAGES,
        payload: [response.hits, response.total]
      })
    })
}

export const fetchSearchText = (input) => dispatch => {
  dispatch({
    type: FETCH_SEARCH_TEXT,
    payload: input,    
  })
}

export const fetchAmount = (input) => dispatch => {
  dispatch({
    type: FETCH_AMOUNT,
    payload: input,
  })
}

export const fetchOrder = (input) => dispatch => {
  dispatch({
    type: FETCH_ORDER,
    payload: input,
  })
}

export const fetchOrientation = (input) => dispatch => {
  dispatch({
    type: FETCH_ORIENTATION,
    payload: input,
  })
}

export const fetchImageType = (input) => dispatch => {
  dispatch({
    type: FETCH_IMAGE_TYPE,
    payload: input,
  })
}

export const fetchCategory = (input) => dispatch => {
  dispatch({
    type: FETCH_CATEGORY,
    payload: input,
  })
}

export const fetchColor = (input = [null]) => dispatch => {
  dispatch({
    type: FETCH_COLOR,
    payload: input,
  })
}

export const fetchPage = (input) => dispatch => {
  console.log('fetchPage');
  dispatch({
    type: FETCH_PAGE,
    payload: input,
  })
}

export const fetchMoreImages = (wait = false) => (dispatch, getState) => {
  console.log('fetchMoreImages');
  let {
    searchText,
    amount,
    order,
    orientation,
    imageType,
    category,
    color,
    page,
  } = getState().search;
  fetch(`https://pixabay.com/api/?key=8913420-89429fef6031f24fd40903778&q=${searchText}&image_type=${imageType}&per_page=${amount}&order=${order}&orientation=${orientation}&category=${category}&colors=${color.join()}&safesearch=true&page=${page}`)
    .then(response => {
      if (response.ok) {
        return response.json()
      }
    })
    .then(response => dispatch({
      type: FETCH_MORE_IMAGES,
      payload: response.hits
    })
    )
    .then(response => {
      if(wait === true){
        dispatch(fetchViewerImg())
      }
    })
    .catch(function (error) {
      dispatch(fetchViewerOpen(false, -1));
      console.log('There has been a problem with fetching more images: ', error.message);
    });
}

export const fetchWindowWidth = (input) => dispatch => {
  dispatch({
    type: FETCH_WINDOW_WIDTH,
    payload: input,
  })
}

export const cutDownSearchStore = () => dispatch => {
  dispatch({
    type: CUT_DOWN_SEARCH_STORE,
  })
}

// type='0' is 'search results'; type='1' is 'favorites'; type='2' is 'recently watched'
export const addToFavorites = (ImgInd, type=0) => (dispatch, getState) => {
  console.log('addToFavorites, ImgInd:', ImgInd);
  let currentImg; 
  let favoritesImgs = getState().search.favorites;
  let checkImgInFavorites;

  // if(!del) {
  //   currentImg = getState().search.images[ImgInd];
  //   checkImgInFavorites = favoritesImgs.findIndex(img => img.id === currentImg.id);
  // } else {
  //   checkImgInFavorites = ImgInd;
  // }

  switch (true) {
    case (type === 0):
      currentImg = getState().search.images[ImgInd];
      checkImgInFavorites = favoritesImgs.findIndex(img => img.id === currentImg.id);
      break;
    case (type === 1):
      currentImg = getState().imgViewer.recentlyWatchedImgs[ImgInd];
      checkImgInFavorites = favoritesImgs.findIndex(img => img.id === currentImg.id);
      break;
    default:
      checkImgInFavorites = ImgInd;
      break;
  }

  if (checkImgInFavorites === -1) {
    favoritesImgs.unshift(currentImg);
  } else {
    favoritesImgs.splice(checkImgInFavorites, 1);
  }
  dispatch({
    type: ADD_TO_FAVORITES,
    payload: favoritesImgs,
  })
}


