import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { fetchImages, fetchMoreImages, cutDownSearchStore } from '../actions/searchAction';
import { fetchPage } from '../actions/searchAction';
import { fetchViewerOpen } from '../actions/imgViewerAction';
import { fetchSearchText } from '../actions/searchAction';
import { addToFavorites } from '../actions/searchAction';

import ImgViewer from './ImageViewer';

import { withStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';

import OpenInNew from '@material-ui/icons/OpenInNew';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import StarIcon from '@material-ui/icons/Star';

const styles = {
  rootComponent: {
    position: 'relative',
    top: 57,
    display: 'block',
    margin: '0px 1px',
  },
  barTitleTag: {
    paddingRight: '0.35em',
    cursor: 'pointer',
    textDecoration: 'none',
    color: '#eee',
    '&:hover': {
      textDecoration: 'underline',
    },
  },
  gridList: {
    justifyContent: 'space-around',
  },
  gridListTile: {
    border: '2px solid rgba(0, 0, 0, 0)',
    borderRadius: '2px',
    transition: 'all 0.1s ease',
    '&:hover': {
      border: '2px solid rgba(0, 0, 0, 0.75)',
    },
    '&:hover .gridListTileBar': {
    // '&:hover ': {
      backgroundColor: 'rgba(0, 0, 0, 0.85)',
    },
  },
  gridImg: {
    cursor: 'zoom-in',
  },
  gridListTileBarRoot: {
    height: 54,
    transition: 'all 0.1s ease',
    backgroundColor: 'rgba(0, 0, 0, 0.4)',
    boxSizing: 'border-box',

    bottom: 0,
    // left: 0,
    // right: 0,
    width: '100%',
    // height: 48,
    display: 'flex',
    position: 'absolute',
    // background: 'rgba(0, 0, 0, 0.4)',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  gridListTileBarTitleWrap: {
    marginLeft: 12,
    marginRight: 7,

    color: '#fff',
    overflow: 'hidden',
    flexGrow: '0',
    width: '100%',
  },
  gridListTileBarTitle: {
    fontSize: '1em',
    lineHeight: '1.2em',

    overflow: 'hidden',
    whiteSpace: 'nowrap',
    textOverflow: 'ellipsis',
  },
  gridListTileBarSubtitle: {
    fontSize: '0.75em',
    lineHeight: '1.2em',

    overflow: 'hidden',
    whiteSpace: 'nowrap',
    textOverflow: 'ellipsis',
  },
  iconButtonWrap: {
    // width: 'auto',
    display: 'flex',  
    alignItems: 'center',
    justifyContent: 'flex-end',
  },
  iconButton:{
    width: '3em',
    height: '3em',
    fontSize: '0.8em',
    paddingRight: 4, 
    flexGrow: '0',
  },
  icon: {
    fontSize: '2em',
    color: '#fff',
    transition: 'all 0.1s linear',
    '&:hover': {
      fontSize: '3em',
    },
  },
  noMatchesWrapper: {
    display: 'flex',
    posititon: 'fixed',
    top: '0',
    left: '0',
    width: '100%',
    height: '80vh',
    alignItems: 'center',
    justifyContent: 'center',
  },
  noMatches: {
    width: '90vw',
    textAlign: 'center',
    fontSize: '2.5em',
    fontWeight: 'bold',
    color: '#aaa',
  },
  socialData: {
    color: '#fff',
    fontSize: '1.2em',
    flexGrow: '0',
    marginRight: '-3px',
    float: 'right',
  },
};

class SearchResults extends Component {
  constructor(props) {
    super(props);
    // this.state = {
    //   // bottomReached: false,
    //   // showRefreshIndicator: false,
    // };
    this.handleScroll = this.handleScroll.bind(this);
  }

  componentDidMount() {
    console.log('addScrollListener');
    window.addEventListener('scroll', this.handleScroll);
  }

  componentWillUnmount() {
    console.log('removeScrollListener');
    window.removeEventListener('scroll', this.handleScroll);
    this.props.cutDownSearchStore();
  }

  handleScroll() {
    console.log('SearchResults handleScroll');
    if (this.props.totalHits > this.props.images.length) {
      const body = document.body;
      const html = document.documentElement;
      // get height of the window 
      const windowHeight = "innerHeight" in window ? window.innerHeight : document.documentElement.offsetHeight;
      // calculate whole document height
      const docHeight = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight, html.offsetHeight);
      // calculate bottom of window
      const windowBottom = windowHeight + window.pageYOffset;
      // if window bottom is greater than or equal to document height then user reached to bottom
      if (windowBottom >= docHeight) {
        this.props.fetchPage(this.props.page + 1);
        this.props.fetchMoreImages();
      }
    }
  };

  handleImgOpen = (index) => {
    this.props.fetchViewerOpen(true, index);
  }

  handleAddToFavorites = (index) => {
    this.props.addToFavorites(index);
    this.forceUpdate();
  }

  handleOnTagClick = (tag) => {
    this.props.fetchSearchText(tag);
    this.props.fetchPage(1);
    this.props.fetchImages();
  }

  formatData = (data) => {
    let formattedData;
    switch (true) {
      case (data < 1000):
        formattedData = data;
        break;
      case (data < 1000000):
        formattedData = (data / 1000).toFixed(0) + 'K';
        break;
      default:
        formattedData = (data / 1000000).toFixed(0) + 'M';
        break;
    }
    return formattedData
  }
  
  render() {
    console.log('Start render SearchResults');
    console.log('favorites', this.props.favorites);

    let imageListContent;
    let imageViewer;
    let cellHeight;

    const { images, /*totalHits,*/ searchText, favorites, classes } = this.props;

    const gridCols = () => {
      if(this.props.orientation === 'horizontal') {
        cellHeight = 180;
        switch (true) {
          case (this.props.designType === 1):
            return 5
          case (this.props.designType === 2):
            return 4
          case (this.props.designType === 3):
            return 3
          case (this.props.designType === 4):
            return 2
          default:
            return 1
        }
      } else if (this.props.orientation === 'vertical') {
        cellHeight = 270;
        switch (true) {
          case (this.props.designType === 1):
            return 8
          case (this.props.designType === 2):
            return 6
          case (this.props.designType === 3):
            return 4
          case (this.props.designType === 4):
            return 3
          default:
            return 2
        }
      } else {
        // 'else {..}' need update!
        cellHeight = 180;
        switch (true) {
          case (this.props.designType === 1):
            return 5
          case (this.props.designType === 2):
            return 4
          case (this.props.designType === 3):
            return 3
          case (this.props.designType === 4):
            return 2
          default:
            return 1
        }
      }
    }

    let isFavorite = (imgId) => favorites.findIndex(favImg => favImg.id === imgId);

    if (images.length) {      
      imageListContent = (
        <GridList
          className={classes.gridList}
          style={{margin: 0}}
          cols={gridCols()} 
          cellHeight={cellHeight} 
        >
          {images.map((img, index) => ( 
            <GridListTile 
              className={classes.gridListTile}
              style={{padding: 0}}
              key={index + '_' + img.id} 
            >
              <img
                className={classes.gridImg}
                src={img.webformatURL}
                alt={img.tags}
                onClick={() => this.handleImgOpen(index)}
              />
              {/* <GridListTileBar
                className='gridListTileBar'
                classes={{
                  root: classes.gridListTileBarRoot,
                  titleWrap: classes.gridListTileBarTitleWrap,
                  title: classes.gridListTileBarTitle,
                  subtitle: classes.gridListTileBarSubtitle,
                }}
                title={ (() => {
                  let tagArray;
                  if (img.tags.split(',').length > 1) {
                    tagArray = img.tags.split(',')
                  } else { 
                    tagArray = img.tags.split(' ') 
                  }
                  return (
                    tagArray.map((tag, tagIndex)=> (
                      <span
                        className={classes.barTitleTag}
                        key={tagIndex}
                        title={`search for '${tag.trim()}'`}
                        onClick={() => this.handleOnTagClick(tag.trim())}
                      >
                        #{tag.trim()}
                      </span>
                    )))})()
                }
                // subtitle={<span className='imgGridSubtitle'>by {img.user}</span>}
                subtitle={
                  <span>by: <a className={classes.barTitleTag}
                      href={`https://pixabay.com/users/${img.user}-${img.user_id}/`}
                      target='_blank'
                      title={`'${img.user}' profile at Pixabay`}
                    >
                      {img.user}
                    </a>
                  </span>
                }
                actionIcon={
                  <div className={classes.iconButtonWrap}>
                    <span className={classes.socialData}> {this.formatData(img.favorites)} </span>
                    <IconButton 
                      className={classes.iconButton}
                      onClick={() => this.handleAddToFavorites(index)}
                      title={!~isFavorite(img.id) ? 'add to Favorites' : 'remove from Favorites'}
                    >
                      {!~isFavorite(img.id) ? <StarBorderIcon className={classes.icon} /> : <StarIcon className={classes.icon} />}
                    </IconButton>
                  </div>
                  //   <IconButton
                  //     className={classes.iconButton}
                  //     // onClick={() => console.log(img.pageURL)}
                  //     title='see at Pixabay'
                  //     href={img.pageURL}
                  //     target='_blank'
                  //   >
                  //     <OpenInNew className={classes.icon} />
                  //   </IconButton>
                }
              /> */}
              
              <div className={`${classes.gridListTileBarRoot} gridListTileBar`}>
                <div className={classes.gridListTileBarTitleWrap}> 
                  <div className={classes.gridListTileBarTitle}>
                    {
                      (() => {
                        let tagArray;
                        if (img.tags.split(',').length > 1) {
                          tagArray = img.tags.split(',')
                        } else {
                          tagArray = img.tags.split(' ')
                        }
                        return (
                          tagArray.map((tag, tagIndex)=> (
                          <span
                          className={classes.barTitleTag}
                          key={tagIndex}
                          title={`search for '${tag.trim()}'`}
                          onClick={() => this.handleOnTagClick(tag.trim())}
                        >
                          #{tag.trim()}
                        </span>
                      )))})()
                    }
                  </div>
                  <div className={classes.gridListTileBarSubtitle}>
                    {
                      <span>
                        by: <a className={classes.barTitleTag}
                          href={`https://pixabay.com/users/${img.user}-${img.user_id}/`}
                          target='_blank'
                          title={`'${img.user}' profile at Pixabay`}
                        >
                          {img.user}
                        </a>
                      </span>
                    }
                  </div>
                </div>
                <div>
                  <span className={classes.socialData}>
                    {this.formatData((!~isFavorite(img.id) ? img.favorites : (img.favorites+1)))}
                  </span>
                </div>
                {/* <div className={classes.iconButtonWrap}> */}
                  {/* <div className={classes.socialData}> {this.formatData(img.favorites)} </div> */}
                  <IconButton
                    className={classes.iconButton}
                    onClick={() => this.handleAddToFavorites(index)}
                    title={!~isFavorite(img.id) ? 'add to Favorites' : 'remove from Favorites'}
                  >
                    {!~isFavorite(img.id) ? <StarBorderIcon className={classes.icon} /> : <StarIcon className={classes.icon} />}
                  </IconButton>
                {/* </div> */}
              </div>

            </GridListTile  >
          ))}
        </GridList>
      )
    } else if (searchText) {
      imageListContent = (
        <div className={classes.noMatchesWrapper}>
          <div className={classes.noMatches}>
            no matches for "{searchText}"
          </div>
        </div>
      )
    }

    if (this.props.open) {
      imageViewer = <ImgViewer />
     }
    
    return (
      <div className={classes.rootComponent}>
        {imageListContent}
        {imageViewer}
      </div>
    )
  }
}

SearchResults.propTypes = {
  fetchImages: PropTypes.func,
  fetchMoreImages: PropTypes.func,
  fetchPage: PropTypes.func,
  fetchViewerOpen: PropTypes.func,
  fetchSearchText: PropTypes.func,
  cutDownSearchStore: PropTypes.func,
  addToFavorites: PropTypes.func,
  images: PropTypes.array,
  totalHits: PropTypes.number,
  page: PropTypes.number,
  designType: PropTypes.number,
  orientation: PropTypes.string,
  open: PropTypes.bool,
  searchText: PropTypes.string,
  favorites: PropTypes.array,
  classes: PropTypes.object.isRequired,
}

const mapStateToProps = state => ({
  images: state.search.images,
  totalHits: state.search.total,
  page: state.search.page,
  designType: state.search.designType,
  orientation: state.search.orientation,
  open: state.imgViewer.open,
  searchText: state.search.searchText,
  favorites: state.search.favorites,
})

const mapDispatchToProps = {
  fetchImages,
  fetchMoreImages,
  fetchPage,
  fetchViewerOpen,
  cutDownSearchStore,
  addToFavorites,
  fetchSearchText,
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(SearchResults))


